import React, { useEffect } from "react";
import { Outlet, useLocation, useNavigate } from "react-router-dom";

import CONST from "constants/app";
import Breadcrumb from "components/Breadcrumb";
import { deleteCookie, getCookie } from "helpers/cookie";

function App() {
  const navigate = useNavigate();
  const location = useLocation();
  const visitedPage = getCookie(CONST.cookie.visitedPage);

  useEffect(() => {
    if (visitedPage) {
      deleteCookie(CONST.cookie.visitedPage);
      // remove any trailing slash "/" and navigate
      navigate(visitedPage.replace(/\/?$/, ""));
    }
    // Run only once in App's lifetime
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (visitedPage) {
    // Avoid FOMW (Flash Of My Workaround) for direct URL hits
    return null;
  }

  return (
    <>
      {location.pathname !== CONST.pages.home.path && (
        <Breadcrumb path={location.pathname} />
      )}
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

export default App;
