import BreadCrumb from "components/Breadcrumb";
import React from "react";
import { Link, useNavigate } from "react-router-dom";

import useSiteSearch from "./hooks";

export default function HomeSearch() {
  const { inputValue, searchResults, handleInputValueChange } = useSiteSearch();
  const navigate = useNavigate();

  return (
    <>
      <div className="shadow-xl fixed top-10">
        <input
          autoFocus
          type="text"
          className="p-2 pl-10 rounded border sm:text-xl text-lime-700 font-bold tracking-wide bg-white focus:bg-white focus:outline-none border-transparent"
          placeholder="search..."
          value={inputValue}
          onChange={handleInputValueChange}
        />
        <svg
          className="w-4 h-4 absolute left-2.5 top-3.5"
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
          />
        </svg>
      </div>
      <div className="bg-gray-50 w-full mt-32 shadow rounded">
        {searchResults &&
          searchResults.map(({ item }) => {
            return (
              <div
                key={item.title}
                className="px-5 py-3 border-b-2 last:border-0 border-neutral-200 hover:cursor-pointer"
                onClick={() => navigate(item.path)}
              >
                <span className="text-gray-600 font-bold tracking-wide">
                  {item.title}
                </span>
                <BreadCrumb
                  path={item.path}
                  crumbSeparatorClassName="!w-4 !h-4"
                  crumbClassName="!text-xs !text-gray-400"
                  wrapperClassName="!px-2"
                  hideHome
                />
              </div>
            );
          })}
      </div>
    </>
  );
}
