import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import App from "./App";
import lazyComponent from "./components/LazyComponent";
import Home from "./pages/Home";
import CONST from "./constants/app";

export default function AppRoutes() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={CONST.pages.home.path} element={<App />}>
          <Route index element={<Home />} />

          {/* Tools */}
          <Route path={CONST.pages.tools.path} element={<Tools />} />
          {/* Tools::Crypto */}
          <Route
            path={CONST.pages.toolsCrypto.path}
            element={<ToolsCrypto />}
          />
          {/* Tools::Money */}
          <Route path={CONST.pages.toolsMoney.path} element={<ToolsMoney />} />
          <Route
            path={CONST.pages.toolsMoneyEx.path}
            element={<ToolsMoneyCurrencyEx />}
          />
          {/* Tools::Convert */}
          <Route
            path={CONST.pages.toolsConvert.path}
            element={<ToolsConvert />}
          />
          <Route
            path={CONST.pages.toolsConvertTemp.path}
            element={<ToolsConvertTemperature />}
          />
          {/* Travel */}
          <Route path={CONST.pages.travel.path} element={<Travel />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

/**
 * Tools
 */
const Tools = lazyComponent("pages/Tools");
/**
 * Tools::Crypto
 */
const ToolsCrypto = lazyComponent("pages/Tools/Crypto");
/**
 * Tools::Money
 */
const ToolsMoney = lazyComponent("pages/Tools/Money");
const ToolsMoneyCurrencyEx = lazyComponent("pages/Tools/Money/CurrencyEx");
/**
 * Tools::Convert
 */
const ToolsConvert = lazyComponent("pages/Tools/Convert");
const ToolsConvertTemperature = lazyComponent(
  "pages/Tools/Convert/Temperature"
);
/**
 * Travel
 */
const Travel = lazyComponent("pages/Travel");